// Import the functions you need from the SDKs you need
import { initializeApp, FirebaseOptions } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
import { getAuth, connectAuthEmulator } from "firebase/auth";
import { getFirestore, connectFirestoreEmulator, Firestore } from "firebase/firestore";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Firebase configuration from environment variables
const firebaseConfig: FirebaseOptions = {
    apiKey: process.env.NEXT_PUBLIC_FIREBASE_API_KEY,
    authDomain: process.env.NEXT_PUBLIC_FIREBASE_AUTH_DOMAIN,
    projectId: process.env.NEXT_PUBLIC_FIREBASE_PROJECT_ID,
    storageBucket: process.env.NEXT_PUBLIC_FIREBASE_STORAGE_BUCKET,
    messagingSenderId: process.env.NEXT_PUBLIC_FIREBASE_MESSAGING_SENDER_ID,
    appId: process.env.NEXT_PUBLIC_FIREBASE_APP_ID,
    measurementId: process.env.NEXT_PUBLIC_FIREBASE_MEASUREMENT_ID
};

// Log Firebase config for debugging (don't include this in production)
console.log('Firebase config:', {
    projectId: firebaseConfig.projectId,
    authDomain: firebaseConfig.authDomain,
    apiKeyLength: firebaseConfig.apiKey ? firebaseConfig.apiKey.length : 0,
    hasAppId: !!firebaseConfig.appId
});

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Initialize Analytics only in browser environment
let analytics;
if (typeof window !== 'undefined') {
    try {
        analytics = getAnalytics(app);
    } catch (error) {
        // Analytics initialization skipped in development
        console.log('Analytics initialization skipped');
    }
}

// Initialize Firebase Authentication
const auth = getAuth(app);

// Initialize Firestore
let db: Firestore;
try {
    db = getFirestore(app);
    console.log('Firestore initialized successfully with project ID:', firebaseConfig.projectId);

    // Test if we can access the internal Firestore object
    // @ts-ignore - Accessing internal property for debugging
    if (db._delegate) {
        console.log('Firestore internal object available');
    }
} catch (error: any) {
    console.error('Error initializing Firestore:', error);
    console.error('Error details:', {
        message: error.message,
        code: error.code,
        stack: error.stack
    });
    // Provide a placeholder to prevent app crashes
    throw new Error(`Failed to initialize Firestore: ${error.message}`);
}

// Connect to Auth and Firestore Emulator in development mode
if (typeof window !== 'undefined' && process.env.NODE_ENV === 'development' && window.location.hostname === 'localhost') {
    try {
        // connectAuthEmulator(auth, "http://localhost:9099", { disableWarnings: true });
        // connectFirestoreEmulator(db, 'localhost', 8080);
        console.log('Using Firebase in production mode');
    } catch (error) {
        console.error('Error connecting to Firebase emulators:', error);
    }
}

export { app, analytics, auth, db };